/* styles.css */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  background-color: white;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  text-align: center;
}

.app {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}

.header {
  width: 100%; /* Full width */
  padding: 20px 0;
  text-align: center;
  border-bottom: 4px solid #FF7F00; /* Full width orange line */
}

.title {
  font-size: 2.0rem;
  font-weight: bold;
  color: #FF7F00; /* Orange color */
  font-family: 'Press Start 2P', serif;
  margin-bottom: 20px; /* Space between title and line */
}

.profile-img {
  width: 150px; /* Set width of profile picture */
  height: 150px; /* Set height to maintain aspect ratio */
  border-radius: 50%; /* Make the image circular */
  margin-top: 20px; /* Space between the line and profile picture */
  border: 4px solid #FF7F00; /* Optional border around the image */
  object-fit: cover; /* Ensure image covers the space without distorting */
}

/* Link Tree Styles */
.link-tree {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.link-item {
  text-decoration: none;
  font-size: 1.5rem;
  color: #FF7F00; /* Orange color */
  font-family: 'Press Start 2P', serif;
  padding: 10px 20px;
  border: 2px solid #FF7F00;
  border-radius: 5px;
  width: 200px;
  text-align: center;
  transition: background-color 0.3s, color 0.3s;
  cursor: pointer;
}

.link-item:hover {
  background-color: #FF7F00;
  color: white;
}

/* Bitcoin Address Display */
.bitcoin-address-display {
  margin-top: 20px;
}

.bitcoin-address {
  font-size: 0.8rem;
  color: #FF7F00;
  font-family: 'Press Start 2P', serif;
  word-wrap: break-word;
  white-space: pre-wrap; /* Ensure address breaks to the next line if needed */
}

/* Footer Section */
.footer {
  width: 100%;
  margin-top: 50px;
  padding: 20px 0;
  background-color: #f4f4f4;
  text-align: center;
  position: absolute;
  bottom: 0;
}

.bitcoin-section {
  margin-bottom: 20px;
}

.bitcoin-icon {
  width: 10px;
  height: 40px;
  margin-bottom: 10px;
}

.rights-reserved {
  font-size: 0.6rem;
  color: #FF7F00;
  font-family: 'Press Start 2P', serif;
  margin-top: 10px;
}
